.nav-item {
    margin: 0 1.5rem;
    font-weight: 300;
    color: rgb(80, 80, 80);
    transition: 0.15s ease;
}

.nav-item:hover {
    transition: 0.15s ease;
    color: rgb(5, 5, 5);
    cursor: pointer;
}

.nav-item {
    text-decoration: none;
}

@media (max-width: 960px) {
    .nav-item {
        margin: 0 1.5rem;
    }
}