@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:300,400,500);
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Roboto Slab', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin-top: 3.5rem;
  text-align: center;
}
.nav-container {
    background-color: #f8f9fa;
    padding: 1rem 0;
    font-weight: 300;
    font-size: 1.2rem;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 500;
  }
  

.nav-item {
    margin: 0 1.5rem;
    font-weight: 300;
    color: rgb(80, 80, 80);
    transition: 0.15s ease;
}

.nav-item:hover {
    transition: 0.15s ease;
    color: rgb(5, 5, 5);
    cursor: pointer;
}

.nav-item {
    text-decoration: none;
}

@media (max-width: 960px) {
    .nav-item {
        margin: 0 1.5rem;
    }
}
.logo {
    margin-right: 40rem;
    margin-bottom: -0.5rem;
    transition: 0.3s ease;
}

img {
    width: 175px;
}

@media (max-width: 1300px) {
    .logo {
        transition: 0.3s ease;
        margin-right: 25rem;
    }
}

@media (max-width: 1050px) {
    .logo {
        transition: 0.3s ease;
        margin-right: 15rem;
    }
}

@media (max-width: 850px) {
    .logo {
        transition: 0.3s ease;
        margin-right: 10rem;
    }
}


@media (max-width: 768px) {
    .logo {
        display: none;
    }
}
.header {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: #daf6ff;
}

.header p {
    font-weight: 300;
    font-size: 1.3rem;
    padding: 0;
    margin: 0.5rem 0 0 0;
}

.header p.large {
    font-size: 2.5rem;
    font-weight: 400;
    margin-top: 1rem;
}

b {
    font-weight: 500;
}

.header img {
    width: 140px;
    border: 5px solid white;
    border-radius: 50%;
    margin-bottom: 1rem;
}

@media (max-width: 768px) {
    .header p {
        margin: 0.25rem 0 0 0;
    }
}
.about {
    display: flex;
    justify-content: center;
}

.about-contents {
    text-align: left;
    width: 73.5rem;
}

.about-contents p {
    font-weight: 300;
    font-size: 1.25rem;
}

h1 {
    font-size: 2.25rem;
    font-weight: 400;
}

.about-contents a {
    text-decoration: none;
    color: black;
    border-bottom: 2px dotted grey;
    transition: 0.2s ease;
}

.about-contents a:hover {
    text-decoration: none;
    color: rgb(0, 127, 247);
    border-bottom: 2px dotted transparent;
    transition: 0.2s ease;
}

@media (max-width: 1300px) {
    .about-contents {
        transition: 0.3s ease;
        width: 58.5rem;
    }
}

@media (max-width: 1050px) {
    .about-contents {
        transition: 0.3s ease;
        width: 48.5rem;
    }

    .about-contents p {
        font-size: 1.2rem;
    }
}

@media (max-width: 850px) {
    .about-contents {
        transition: 0.3s ease;
        padding: 0 2rem;
        width: 100%;
    }
    
    .about-contents p {
        font-size: 1.17rem;
    }
}

@media (max-width: 768px) {
    .about-contents {
        text-align: center;
        width: 100%
    }
}
.work {
    display: flex;
    justify-content: center;
}

.work-contents {
    text-align: left;
    width: 73.5rem;
}

h1 {
    font-size: 2.5rem;
    font-weight: 400;
}

@media (max-width: 1300px) {
    .work-contents {
        transition: 0.3s ease;
        width: 58.5rem;
    }
}

@media (max-width: 1050px) {
    .work-contents {
        transition: 0.3s ease;
        width: 48.5rem;
    }
}

@media (max-width: 850px) {
    .work-contents {
        transition: 0.3s ease;
        padding: 0 2rem;
        width: 100%;
    }
}

@media (max-width: 768px) {
    .work-contents {
        text-align: center;
        width: 100%
    }
}
.work-card {
    border-radius: 2px;
    border: 1px solid grey;
    padding: 1.5rem 4rem;
    box-shadow: -1px 1px 4px grey;
    margin-bottom: 2rem;
    transition: 0.3s ease;
}

.work-card:hover {
    box-shadow: -2px 2px 7px grey;
    transition: 0.3s ease
}

.work-card h2 {
    margin: 0 0 0.2rem 0;
    font-weight: 500;
    font-size: 1.75rem;
}

.work-card h3 {
    margin: 0;
    font-weight: 400;
    font-size: 1.3rem;
}

.work-card h3.date {
    margin-bottom: 0.35rem;
    font-weight: 400;
    font-size: 1rem;
}

.work-card p {
    margin: 1rem 0 0 0;
    font-weight: 300;
    font-size: 1.25rem;
    line-height: 1.75rem;
}

.underline {
    display: inline-block;
    border-bottom: 2px dotted grey;
    transition: 0.2s ease
}

.underline:hover {
    color: rgb(0, 127, 247);
    border-bottom: 2px dotted white;
    transition: 0.2s ease;
    cursor: pointer;
}

.work-card a {
    text-decoration: none;
    color: inherit;
}

.work-card a:visited {
    text-decoration: none;
    color: inherit;
}

@media (max-width: 1300px) {
    .work-card {
        padding: 1rem 3rem;
        transition: 0.3s ease;
    }
}

@media (max-width: 1050px) {
    .work-card {
        padding: 1rem 2rem;
        transition: 0.3s ease;
    }
}

@media (max-width: 576px) {
    .work-card {
        padding: 1rem;
        transition: 0.3s ease;
    }

    .work-card h2 {
        font-size: 1.7rem;
    }

    .work-card p {
        font-size: 1.2rem;
    }
}
.projects {
    display: flex;
    justify-content: center;
    padding-bottom: 1rem;
}

.projects-contents {
    text-align: left;
    width: 73.5rem;
}

h1 {
    font-size: 2.5rem;
    font-weight: 400;
}

@media (max-width: 1300px) {
    .projects-contents {
        transition: 0.3s ease;
        width: 58.5rem;
    }
}

@media (max-width: 1050px) {
    .projects-contents {
        transition: 0.3s ease;
        width: 48.5rem;
    }
}

@media (max-width: 850px) {
    .projects-contents {
        transition: 0.3s ease;
        padding: 0 2rem;
        width: 100%;
    }
}

@media (max-width: 768px) {
    .projects-contents {
        text-align: center;
        width: 100%
    }
}
